import {Component, OnInit} from '@angular/core';
import {GlobalService} from 'src/app/core/services/global/global.service';
import {ToastService} from 'src/app/core/services/toast/toast.service';
import {Router} from '@angular/router';
import {ApicallService} from 'src/app/core/services/apicall/apicall.service';
import {AlertController, LoadingController} from '@ionic/angular';
@Component({
  selector: 'app-add-doctor',
  templateUrl: './add-doctor.page.html', 
  styleUrls: ['./add-doctor.page.scss']
})
export class AddDoctorPage implements OnInit {
    public doctordata : any = {
        name: '',
        qualification: '',
        date_of_birth:'',
        date_of_marrige:'',
        hospital: '',
        contact_no: '',
        city: '',
        rout: ''
    }
    public Doctor : any;
    public allCities : any;
    public allRout : any;
    public stateData : any = "addData";
    constructor(
      public global : GlobalService,
      public apicall : ApicallService, 
      public toast : ToastService, 
      public route : Router, 
      private loadingCtrl : LoadingController,
      private alertController: AlertController
      ) {}

    ngOnInit() {
        this.getAllCities();
        if (history.state.data !== undefined) {
            this.stateData = "updateData";
            this.doctordata = history.state.data;
            console.log(this.doctordata);
            setTimeout(() => {
                this.stateData = "addData";
                console.log(this.stateData);
            }, 1000);
        }
    }
    allow() {
        this.stateData = "addData"
    }
    async getAllCities() {
        await this.apicall.api_getCities();
        this.global.GetCities.subscribe(res => {
            this.allCities = res;
        })
    }
    // async getAlldoctorrout(){
    // await this.apicall.api_getDoctorrout();
    // this.global.GetRout.subscribe(res =>{
    //     this.allRout = res;
    // })
    // }
    async adddata() {
        if (history.state.data !== undefined) {
            this.showLoading();
            await this.apicall.api_updatedoctor(this.doctordata);
            this.doctordata = {
                name: '',
                qualification: '',
                date_of_birth:'',
                date_of_marrige:'',
                hospital: '',
                contact_no: '',
                city: '',
                rout: ''
            }
            await this.apicall.api_getDoctor();
            this.global.Doctor.subscribe(res => {
                this.Doctor = res;
            });
            // this.getAlldoctorrout();
            setTimeout(() => {
                this.route.navigate(['manage-doctors']);
            }, 1000);
        } else {
            console.log(this.doctordata);
            await this.apicall.api_adddoctor(this.doctordata);
            this.doctordata = {
                name: '',
                qualification: '',
                date_of_birth:'',
                date_of_marrige:'',
                hospital: '',
                contact_no: '',
                city: '',
                rout: ''
            }
            await this.apicall.api_getDoctor();
            this.global.Doctor.subscribe(res => {
                this.Doctor = res;
            });
            // this.getAlldoctorrout();
        }
    }

    async showLoading() {
        const loading = await this.loadingCtrl.create({message: 'Loading...', duration: 1000, spinner: 'circles'});

        loading.present();
    }

    

}
